import mqtt from "mqtt"

class MqttApi {
  url = 'ws://localhost:9001/';

  options = {
    clean: true,
    connectTimeout: 4,
    clientId: 'client',
  }

  send(data) {
    let mqttclient = mqtt.connect(this.url, this.options);
    console.log('hello')
    mqttclient.on('connect', function (packet) {
      mqttclient.publish("SpringBootRequest", data, {qos: 2});
      
      // timeout
      setTimeout(function() {
        mqttclient.end()
      }, 3000)
    });
  }
}

export default new MqttApi();